import React from 'react'
import { graphql } from 'gatsby'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import { TbUsers, TbTools, TbWand, TbMoodConfuzed, TbStairsUp, TbPennant } from 'react-icons/tb'
import { MdFormatQuote } from 'react-icons/md'
import MockupGallery from '../components/MockupGallery'

const CaseStudiesTemplate = ({data:{contentfulCaseStudies}}) => {
    const { projectOverview,
        projectDuration,
        problem,
        goal,
        myRole,
        responsibilities,
        projectOverviewImage,
        heroImage,
        userResearchSummary,
        painPoints,
        personas,
        // userJourneyMap
        paperWireframes,
        digitalWireframes,
        lowFidelityPrototype,
        usabilityStudyFindings,
        usabilityStudyMockups,
        mockups,
        highFidelityPrototype,
        accessibilityConsiderations,
        takeaways,
        caseStudyDate
    } = contentfulCaseStudies

    return (
        <div className='pt-32 pb-16'>
            <section className='bg-black bg'>
                <GatsbyImage className='h-64' image={getImage(heroImage)} alt={heroImage.description} objectFit='cover' />
            </section>
            {/* Project Overview */}
            <section className='grid grid-cols-1 gap-8 mt-12 lg:grid-cols-2 px-8 sm:px-12 md:px-24 lg:px-32'>
                <article className='max-w-2xl py-8'>
                    <h2 className='text-4xl font-extrabold'>Project Overview</h2>
                        <p className='pt-8'>{projectOverview.projectOverview}</p>
                    <h3 className='text-2xl font-bold pt-12'>Project Duration</h3>
                        <p className='pt-4 font-light'>{projectDuration}</p>
                </article>
                <figure>
                    <GatsbyImage className='h-128' image={getImage(projectOverviewImage)} alt={projectOverviewImage.description} objectFit='contain' />
                </figure>
            </section>
            <section className='grid grid-cols-1 gap-12 md:grid-cols-2 lg:grid-cols-4 py-16 px-8 sm:px-12 md:px-24 lg:px-32'>
                <article className='pr-8'>
                    <h3 className='text-2xl font-bold pt-12'>The Problem</h3>
                    <p className='pt-4'>{problem}</p>
                </article>
                <article className='pr-8'>
                    <h3 className='text-2xl font-bold pt-12'>The Goal</h3>
                    <p className='pt-4'>{goal}</p>
                </article>
                <article className='pr-8'>
                    <h3 className='text-2xl font-bold pt-12'>My Role</h3>
                    <p className='pt-4'>{myRole}</p>
                </article>
                <article className='pr-8'>
                    <h3 className='text-2xl font-bold pt-12'>Responsibilities</h3>
                    <p className='pt-4'>{responsibilities}</p>
                </article>
            </section>

            {/* UNDERSTANDING THE USER */}

            <section className=' bg-gray-100 py-16 sm:px-12 md:px-24 lg:px-32 flex justify-center'>
                <article className='max-w-6xl text-center flex flex-col'>
                    <TbUsers className='text-6xl mb-8 text-gray-400 place-self-center' />
                    <h2 className='text-4xl font-extrabold'>UNDERSTANDING THE USER</h2>
                    <p className='pt-8'>{userResearchSummary.userResearchSummary}</p>
                </article>
            </section>
            <section className=' bg-gray-100 p-16 sm:px-12 md:px-24 lg:px-32 grid grid-cols-1 gap-12 lg:grid-cols-2'>
                {painPoints.map((item) => {
                    return (
                        <div key={item.key} className='max-w-xl'>
                            <h3 className='text-2xl font-bold'>{item.key}.</h3>
                            <p className='mt-8'>{item.content}</p>
                        </div>
                    )
                })}
                {/* <GatsbyImage image={getImage(userJourneyMap)} alt={userJourneyMap.description} objectFit='contain' /> */}
            </section>
            <section className='bg-gray-200 py-16 sm:px-12 md:px-24 lg:px-32 grid grid-cols-1 lg:grid-cols-2 gap-12'>
                {personas.map((item) => {
                    return (
                        <GatsbyImage image={getImage(item)} key={item.id} alt={item.description} objectFit='cover' className='drop-shadow-xl' />
                    )
                })}
            </section>

            {/* STARTING THE DESIGN */}

            <section className='py-16 sm:px-12 md:px-24 lg:px-32 flex flex-col justify-center text-center'>
                <TbTools className='text-6xl mb-8 text-gray-400 place-self-center' />
                <h2 className='text-4xl font-extrabold'>STARTING THE DESIGN</h2>
            </section>
            <section className='sm:px-12 md:px-24 lg:px-32 grid grid-cols-2 lg:grid-cols-3 py-8'>
                <article className='m-8'>
                    <h3 className='text-2xl font-bold'>Paper Wireframes</h3>
                    <p className='max-w-xl mt-8'>{paperWireframes.description}</p>
                </article>
                <GatsbyImage className='lg:mt-16 mx-16 col-span-2' image={getImage(paperWireframes)} alt={paperWireframes.description} objectFit='contain' />
            </section>
            <section className='sm:px-12 md:px-24 lg:px-32 m-8'>
                <h3 className='text-2xl font-bold'>Digital Wireframes</h3>
                <div className='mt-12 grid grid-cols-1 lg:grid-cols-2'>
                    {
                        digitalWireframes.map((item) => {
                            return (
                                <article key={item.id} className='mx-12'>
                                    <GatsbyImage className='m-8' image={getImage(item)} alt={item.description} objectFit='contain' />
                                    <div className='flex justify-center mt-4'>
                                        <span className=' text-emerald-600'><TbWand /></span>
                                        <p className='ml-2 font-light text-sm'>{item.description}</p>
                                    </div>
                                </article>
                            )
                        })
                    }
                </div>
            </section>
            <section className='bg-gray-900 sm:px-12 md:px-24 lg:px-32 grid grid-cols-3 py-8 mt-16'>
                <article className='m-8'>
                    <h3 className='text-white text-2xl font-bold'>Low-fidelity Prototype</h3>
                    <p className='text-gray-300 max-w-xl mt-8'>{lowFidelityPrototype.description}</p>
                </article>
                <GatsbyImage className='lg:mt-16 mx-16 col-span-2' image={getImage(lowFidelityPrototype)} alt={lowFidelityPrototype.description} objectFit='contain' />
            </section>
            <section className='sm:px-12 md:px-24 lg:px-32 py-8 m-8'>
                <h3 className='text-2xl font-bold'>Usability Study Findings</h3>
                <p className='max-w-5xl mt-8'>{usabilityStudyFindings.description}</p>
                <div className='mt-8 grid grid-cols-1 lg:grid-cols-2 gap-12'>
                    {usabilityStudyFindings.findings.map((item, index) => {
                        return (
                            <article className='p-8 bg-gray-200 relative' key={'findings-' + index}>
                                <h4 className='text-2xl'>{item.title}</h4>
                                <ul className='list-disc'>
                                    {item.entries.map((subItem, index) => {
                                       return <li className='m-4' key={'findings-entries-' + index}>{subItem}</li>
                                    })}
                                </ul>
                                <TbMoodConfuzed className='absolute bottom-4 right-4 text-8xl text-gray-300' />
                            </article>
                        )
                    })}
                </div>
            </section>

            {/* REFINING THE DESIGN */}

            <section className='bg-gray-200 py-16 sm:px-12 md:px-24 lg:px-32 flex flex-col justify-center text-center'>
                <TbWand className='text-6xl mb-8 text-gray-400 place-self-center' />
                <h2 className='text-4xl font-extrabold mb-12'>REFINING THE DESIGN</h2>

                {usabilityStudyMockups.map((item) => {
                    return (
                        <article key={item.id} className='lg:px-16'>
                            <p className='max-w-3xl m-auto mb-12'>{item.description}</p>
                            <GatsbyImage image={getImage(item)} alt={item.description} objectFit='contain' />
                        </article>
                    )
                })}
            </section>
            <section className='bg-gray-300 pt-16 flex flex-col justify-center text-center'>
                <h3 className='text-3xl font-bold'>Mockups</h3>
                <MockupGallery galleryID='mockup-gallery' images={mockups} />
            </section>
            <section className='bg-gray-900 pt-16 flex flex-col justify-center text-center'>
                <h3 className=' text-white text-3xl font-bold'>High Fidelity Prototype</h3>
                <p className='text-gray-300  max-w-5xl mt-8 m-auto'>{highFidelityPrototype.description}</p>
                <GatsbyImage image={getImage(highFidelityPrototype)} alt={highFidelityPrototype.title} objectFit='contain' className='mx-20 my-12 bg-zinc-900' />
            </section>
            <section className='bg-gray-200 sm:px-12 md:px-24 lg:px-32 py-16'>
                <h3 className='text-2xl font-bold'>Accessibility Considerations</h3>
                <div className='mt-8 grid grid-cols-1 lg:grid-cols-3 gap-24'>
                    {accessibilityConsiderations.accessibility.map((item, index) => {
                        return (
                            <article className='bg-gray-200 relative' key={'accessibility-' + index}>
                                <p>{item}</p>
                            </article>
                        )
                    })}
                </div>
            </section>

            {/* GOING FORWARD */}

            <section className='py-16 sm:px-12 md:px-24 lg:px-32 flex flex-col justify-center'>
                <TbStairsUp className='text-6xl mb-8 text-gray-400 place-self-center' />
                <h2 className='text-4xl font-extrabold text-center'>GOING FORWARD</h2>
                <div className='mt-12 grid grid-cols-2 gap-24'>
                    <article className='p-16'>
                        <h3 className='text-3xl font-bold'>Impact</h3>
                        <p className='mt-8'>{takeaways.Impact}</p>
                    </article>
                    <article className='p-16'>
                        <h3 className='text-3xl font-bold'>What I learned</h3>
                        <p className='mt-8'>{takeaways.What_I_learned}</p>
                    </article>
                </div>
                <div className='bg-gray-100 self-center px-24 py-12 mt-12 relative'>
                    <MdFormatQuote className='text-gray-300 absolute top-1 left-2 text-6xl' />
                    <p className=' max-w-3xl font-extralight text-2xl italic'>{takeaways.quote}</p>
                    <p className='text-right mt-8 font-light'>-- One quote from peer feedback</p>
                </div>
                <span className=' text-gray-700 mt-16 self-center text-4xl'><TbPennant /></span>
                <span className='font-light italic self-center mt-4'>Case Study Date: {caseStudyDate}</span>
            </section>
        </div>
    )
}

export const query = graphql`
  query ($title: String) {
    contentfulCaseStudies(title: {eq: $title}) {
      thumbnail {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
      }
      heroImage {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
      }
      title
      subtitle
      projectOverview {
        projectOverview
      }
      projectDuration
      projectOverviewImage {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
      }
      problem
      goal
      myRole
      responsibilities
      userResearchSummary {
        userResearchSummary
      }
      painPoints {
        key
        content
      }
      personas {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        title
        description
        id
      }
      userJourneyMap {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
      }
      paperWireframes {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
      }
      digitalWireframes {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
        id
      }
      lowFidelityPrototype {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
      }
      lowFidelityPrototypeUrl
      usabilityStudyFindings {
        findings {
          title
          entries
        }
        description
        title
      }
      usabilityStudyMockups {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
        id
      }
      mockups {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        title
        description
        id
      }
      highFidelityPrototype {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        title
        description
      }
      highFidelityPrototypeUrl
      accessibilityConsiderations {
        accessibility
      }
      takeaways {
        Impact
        What_I_learned
        quote
      }
      nextSteps
      caseStudyDate
      id
    }
  }
`

export default CaseStudiesTemplate
